.footer {
  background: $second-bg-color;
}

.footer__title-main {
    color: $text-title-color;
    padding-bottom: 43px;
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;

   @media screen and (min-width: 768px) {
     font-size: 26px;
     line-height: 1.15;
     letter-spacing: 0.04em;
     margin-top: 7px;
     margin-bottom: 16px;
     width: auto;
   }

   @media screen and (min-width: 1280px) {
     font-size: 30px;
     line-height: 1.13;
     letter-spacing: 0.04em;
     margin-bottom: 15px;
     margin-top: 0;
   }
}

  .footer__title-second {
      color: $text-title-color;
      font-family: Titan One;
      font-style: normal;
      font-weight: normal;
      font-size: 34px;
      line-height: 39px;
      letter-spacing: 0.04em;
      text-transform: uppercase;

      @media screen and (min-width: 768px) {
        font-size: 48px;
        letter-spacing: 0.06em;
        width: auto;
      }

      @media screen and (min-width: 1280px) {
        font-size: 58px;
        line-height: 1.14;
      }
  }

.soc-list {
    padding: 0px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style: none;
}

.soc-list__link {
    display: flex;
    height: 44px;
    width: 44px;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    background-color: $socials-bg-color;
    margin-right: 28px;
    // fill: $main-text-color;
    // background-color: rgba(255, 255, 255, 0.1);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .footer__contact-connect {
    display: flex;
    justify-content: flex-end;
    font-weight: 700;
    font-size: 12px;
    line-height: 22px;
    text-align: right;
    color: $main-text-color;
    margin-bottom: 43px;

    @media screen and (min-width: 768px) {
      font-weight: 700;
      font-size: 14px;
      line-height: 1.9;
    }
  }

  .copyright {
    display: block;
  }
  
  .copy-text {
    font-family: DM Sans;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
    color: $second-text-color;
    padding: 22px 91px 25px 79px;
    border-top: 1px solid #e1e1e1;

    @media screen and (min-width: 768px) {
      line-height: 1.9;
      letter-spacing: 0.02em;
      padding: 29px 245px 18px 245px;
    }

    @media screen and (min-width: 1280px) {
      line-height: 1.9;
      letter-spacing: 0.02em;
      padding: 9px 502px 10px 502px;
    }
  }

.footer__title {
  color: $text-title-color;
  padding-bottom: 43px;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
}

.footer__title > span {
  color: $text-title-color;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
